import {getBalance, getBill} from '../../api/api.js';
	export default {
		data() {
			return {
				assetInfo: '',
				listInfo: '',
				pageSize:10,
				isShow:true
			}
		},
		methods: {
			getBalanceData() {
				getBalance().then(data => {
					this.assetInfo = data;
				});
			},
			getBillData() {
				getBill(0).then(data => {
					// console.log(data)
					this.listInfo = data
					
					
				});
			},
			loadMore() {
				getBill(this.pageSize).then(data => {
					// console.log(data)
					if(data == null ){
						this.isShow = false
						return
					}
					this.listInfo = this.listInfo.concat(data)
					this.pageSize += 10
				});
				
			}
		},
		onLoad() {
			this.getBalanceData();
			this.getBillData();
		}
	}
